.radio-field {
  padding-bottom: 20px;
}

// 1-  
.radio-field__input[type="radio"] {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.radio-field__label {
  position: relative;
  display: block;
  padding: 15px 0 15px 75px;

  @include text-heading;

  color: $text-heading-color;
  text-transform: none;
}

//  2   -  
.radio-field__label::before {
  content: "";
  position: absolute;
  width: 48px;
  height: 48px;
  border-radius: 50%;
  top: 0;
  left: 0;
  background: $color-secondary;
}

// 3    - 
.radio-field__label::after {
  content: "";
  position: absolute;
  top: center;
  left: 0;
  margin-left: 15px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  background: $text-primary-color;
}

// 4        ( )      -  ,         
.radio-field__input + .radio-field__label::before {
  background-color: $color-secondary;
}

.radio-field__input + .radio-field__label::after {
  content: none;
}

// 5       ( ( )), 
.radio-field__input:hover + .radio-field__label::before {
  background-color: $color-secondary-hover;
}

.radio-field__input:hover + .radio-field__label::after {
  content: "";
}

// 6       ( ( )), c  
.radio-field__input:active + .radio-field__label::before {
  background-color: $color-secondary-active;
}

.radio-field__input:checked + .radio-field__label::after {
  content: "";
  background-color: #a4a4a4;
}

